<template>
  <div id="open_box_tip" style="z-index: 15">
    <div class="close bgIcon" @click="closeOpenBox"></div>
    <div class="open_share iconSize posC">
      <img src="../../assets/img/task/open_box_tip.png" alt="">
    </div>
    <div class="tip posC">{{rewardParams.coin}}</div>
    <div class="value posC">{{rewardParams.coin}}</div>
    <div class="share_button share_button1 bgIcon posC" @click="showGuildShare">
      <div class="text1">{{rewardParams.treasure_box_share_coin}}</div>
      <div class="text2"></div>
    </div>
    <div class="share_button share_button2 bgIcon posC" @click="toUrl">
      <div class="text1">{{rewardParams.inviting_friend_first_coin}}</div>
      <div class="text2"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    rewardParams: {
      type: Object,
      default: null
    }
  },
  data () {
    return {

    }
  },
  methods: {
    closeOpenBox () {
      console.log('x')
      this.$emit('closeOpenBox')
    },
    // 
    toUrl () {
      this.$router.push('/invite_friend')
    },
    showGuildShare () {
      if (this.$utils.isWx()) {
        this.$emit('boxshare')
      } else {
        if (this.$config.source_style >= 6) {
          this.$emit('boxshare')
        } else {
          // 
          this.closeOpenBox()
          this.$router.push('/short_link?tag=treasure_box')
        }
      }
    }
  },
  mounted () {

  }
}
</script>

<style lang="scss" scoped>
#open_box_tip{
  background: rgba($color: #000000, $alpha: .75);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom:0;
  z-index: 5;
  .close {
    position: absolute;
    top: 90px;
    right: 50px;
    width: 46px;
    height: 46px;
    background-image: url('../../assets/img/task/close.png')
  }
  .open_share{
    width:468px;
    height: 441px;
    top: 178px;
  }
  .tip{
    color: #fff;
    font-size: 24px;
    top: 600px;
  }
  .value{
    font-size: 64px;
    color: #FFFFFF;
    top: 675px;
  }
  .share_button {
    line-height: 1.5;
    box-sizing: border-box;
    text-align: center;
    .text1 {
      font-size: 28px;
    }
    .text2 {
      font-size: 20px;
    }
  }
  .share_button1 {
    padding-top: 30px;
    width: 452px;
    height: 133px;
    color: #FF3232;
    background-image: url('../../assets/img/task/task_button_share.png');
    top: 775px;
  }
  .share_button2 {
    padding-top: 15px;
    width: 452px;
    height: 110px;
    color: #FFFFFF;
    background-image: url('../../assets/img/task/task_button.png');
    top:958px;
  }
}
</style>



