<template>
  <div id="pushBox">
    <!-- app -->
    <div class="messageBox posC bgIcon" v-if="$utils.isWx()">
      <div class="qrlink iconSize">
        <img :src="`${$config.currentApi}/${$config.vsn}/wechat/qrcode?invitation_code=${$route.query.inviteCode}`" alt="">
        <div class="logo"></div>
      </div>
      <div class="closeImg bgIcon" @click="closePage"></div>
    </div>
    <!-- app -->
    <div class="messageBox posC bgIcon isApp" v-else>
      <div class="pushText posC">App</div>
      <div class="closeImg bgIcon" @click="closePage"></div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    this.qrcode = this.$config.currentApi + '/v1/wechat/qrcode'
    return {

    }
  },
  methods: {
    closePage () {
      this.$emit('closePage')
    }
  }
}
</script>
<style lang="scss" scoped>
#pushBox{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  background: rgba($color: #000000, $alpha: .7);
  .closeImg{
    width: 58px;
    height: 58px;
    position: absolute;
    left: 50%;
    bottom: -100px;
    transform: translateX(-50%);
    background-image: url('../../assets/img/task/close.png');
    background-size: 45px 45px;
  }
  .messageBox{
    width: 651px;
    height: 700px;
    margin-top: -100px;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url('../../assets/img/task/wechat_push.png') no-repeat left top;
    background-size: 100%;
    .qrlink  {
      width: 347px;
      height: 347px;
      top: 180px;
      left: 50%;
      transform: translate(-50%);
      position: absolute;
      z-index: 12;
      .logo {
        position: absolute;
        width: 70px;
        height: 70px;
        border-radius: 10px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        background: url('../../assets/img/logo.png') no-repeat center;
        background-size: 100%;
      }
    }
  }
  .messageBox.isApp{
    width: 651px;
    height: 700px;
    background: url('../../assets/img/task/app_push.png') no-repeat left top;
    background-size: 100%;
    top: 50%;
    transform: translate(-50%, -50%);
    margin-top: -100px;
    .pushText {
      bottom: 30px;
      width:490px;
      text-align: center;
      font-size: 32px;
      line-height: 45px;
    }
  }
}
</style>



