
<template>
  <div id="coin">
    <div class="flex-row coinList jusB">
      <div v-for="(item,index) in signData.list" class="coinItem flex-column" :key="index" :class="{isSign: signData.sign_list && (index < signData.sign_list.length)}">
        <div class="coin iconSize isSign">
          <img src="../../assets/img/task/coin.png" width="80" alt="">
          <!-- <img src="../../assets/img/task/coin_gray.png" v-else width="80" alt=""> -->
          <div class="activeIcon" v-if="signData.sign_list && (index < signData.sign_list.length)"></div>
          <div class="text posC">{{item}}</div>
        </div>
        <div class="day">{{index + 1}}</div>
      </div>
    </div>
    <div class="centerLine"></div>
  </div>
</template>
<script>
export default {
  props: {
    signData: {
      type: Object
    }
  }
}
</script>
<style lang="scss" scoped>
#coin{
  position: absolute;
  left: 0;
  right: 0;
  padding: 0 12px;
  height: 158px;
  overflow: hidden;
  .coinItem{
    position: relative;
    .coin{
      width: 82px;
      height: 82px;
      position: relative;
      .activeIcon{
        width: 27px;
        height: 27px;
        background: url('../../assets/img/task/coin_active.png') no-repeat center;
        background-size: 100%;
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 3;
      }
      .text{
        top: 50%;
        transform: translate(-55%, -55%);
        -webkit-transform: translate(-55%, -55%);
        font-size: 22px;
        color: #696969;
        text-align: center;
      }
    }
    .coin.isSign{
      .text{
        color: #AA5200;
      }
    }
    .day{
      font-size: 26px;
      color: #FBB50B;
      text-align: center;
      margin-top: 16px;
    }
    &::after{
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      content: '';
      background: rgba(255,255,255, .5);
      background-size: 100% 100%;
      z-index: 20;
    }
    &.isSign::after{
      display:none
    }
  }
  .coinList{
    position: absolute;
    left: 12px;
    right: 12px;
    z-index: 2;
  }
  .centerLine{
    position: absolute;
    left: 50px;
    right: 50px;
    top: 38px;
    transform: translateY(-50%);
    -webkit-transform: translate(0, -50%);
    height: 4px;
    background: #D8D8D8;
  }
}
</style>



