<template>
  <div id="openBox" class="bgIcon" @click="openBox">
    <div class="countTime flex-row">
      <span v-if="commonState.login_bol">{{remainTime > 0 ? countTime: ''}}</span>
      <span v-else></span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    isChange: {
      type: Number
    }
  },
  data () {
    return {
      remainTime: 0,
      countTime: '',
      isShow: false
    }
  },
  computed: {
    commonState: function () {
      return this.$store.state.common
    }
  },
  methods: {
    initData () {
      if (!this.commonState.login_bol) {
        return
      }
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.$api.get('OPEN_BOX_TIME', {}, res => {
        if (res.data <= 0) {
          this.remainTime = 0
        } else {
          this.remainTime = res.data
          this.countTime = this.$utils.getTimeText(this.remainTime)
          this.$emit('getBoxTime', this.countTime)
          this.timer = setInterval(() => {
            if (this.remainTime <= 0) {
              clearInterval(this.timer)
              this.timer = null
            }
            this.remainTime--
            this.countTime = this.$utils.getTimeText(this.remainTime)
            this.$emit('getBoxTime', this.countTime)
          }, 1000)
        }
        this.isShow = true
      })
    },
    // 
    openBox () {
      if (!this.commonState.login_bol) {
        this.$store.commit('common_login_box', true)
        return
      }
      if (this.remainTime <= 0) {
        this.$emit('openBox')
      }
    }
  },
  watch: {
    isChange: function (a) {
      this.initData()
    },
    'commonState.login_box': function (flag) {
      if (!flag && this.commonState.login_bol) {
        this.initData()
      }
    },
    $route (n) {
      if (n.path === '/task') {
        this.initData()
      }
    }
  },
  created () {
    this.initData()
  },
  destroyed () {
    if (this.timer) {
      clearInterval(this.timer)
      this.timer = null
    }
  }
}
</script>

<style lang="scss" scoped>
#openBox{
  position: fixed;
  bottom: 150px;
  width: 180px;
  height: 165px;
  right: 20px;
  background-image: url('../../assets/img/task/open_box.png');
  background-size: 100% 100%;
  .countTime{
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 42px;
    color:#fff;
    bottom: -21px;
    background-image: linear-gradient(-180deg, #F9B942 0%, #8C0FFF 100%);
    border-radius: 23px;
    font-size: 30px;
    color: #FFFFFF;
    z-index: 3;
  }
}
</style>



