<template>
  <div id="confirm" @click="closeConfirm">
    <div class="confirm bgIcon posC" v-if="$utils.isWx()" @click.stop="''">
      <div class="qrcode iconSize">
        <img :src="`${$config.currentApi}/v1/wechat/qrcode?invitetion_code=${commonState.userInfo.invitation}`" alt="">
        <div class="logo">
          <img src="../../assets/img/logo.png" alt="logo">
        </div>
      </div>
      <div class="bgwhite tip ft28">
        <div></div>
        <div>{{30 * commonState.inviteVcoin}}</div>
      </div>
      <div class="flex-row confirmSuccess"></div>
      <div class="close"  @click="closeConfirm"></div>
    </div>
    <div class="broswer posC" v-else>
      <div class="maskConfirm" @click.stop=""></div>
      <div class="title"></div>
      <div class="official flex-row">
        <div class="logo iconSize">
          <img src="../../assets/img/logo.png" alt="logo">
        </div>
        <div class="right">
          <div class="top"></div>
          <div class="wechat">juejinbaozixun</div>
        </div>
      </div>
      <div class="clipbtn button" @click="copyText" data-clipboard-action="copy" :data-clipboard-text="officialText"></div>
      <div class="confirmTip"><br>{{30 * commonState.inviteVcoin}}</div>
      <div class="close" @click="closeConfirm"></div>
    </div>
  </div>
</template>
<script>
import Clipboard from 'clipboard'
export default {
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      officialText: 'juejinbaozixun'
    }
  },
  methods: {
    closeConfirm () {
      this.$emit('closeConfirm')
    },
    copyText () {
      this.copyText = `juejinbaozixun`
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.clipbtn')
        clb.on('success',  (e) => {
          this.$toast({
            message: '',
            duration: 1500
          })
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '',
            duration: 1500
          })
          clb.destroy()
        })
      } else {

      }
    }
  },
  created () {

  }
}
</script>
<style lang="scss" scoped>
#confirm{
  position: fixed;
  height: 100%;
  overflow: hidden;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 7;
  background: rgba($color: #000000, $alpha: .56);
  color: #fff;
  .confirm {
    top: 50%;
    transform: translate(-50%, -50%);
    color: #333;
    width: 600px;
    border-radius: 14px;
    font-size: 28px;
    background: #fff;
    margin-top: -100px;
    .confirmSuccess{
      font-size: 24px;
      padding: 26px 0 30px;
      font-weight: 900;
      color: #666;
    }
    .tip{
      text-align: center;
      line-height: 40px;
      font-size: 28px;
      font-weight: 900;
    }
    .qrcode{
      width: 260px;
      height: 260px;
      position: relative;
      margin: 55px auto 12px;
      .logo {
        width: 72px;
        height: 72px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .broswer {
    width: 563px;
    box-sizing: border-box;
    color: #333;
    background: #fff;
    margin-top: -100px;
    border-radius: 14px;
    text-align: center;
    top: 50%;
    transform: translate(-50%, -50%);
    .title {
      padding: 64px 0 28px;
      font-weight: 900;
      font-size: 28px;
    }
    .official {
      width: 480px;
      background: #FFFFFF;
      box-shadow: 0 2px 11px 0 rgba(245,166,35,0.31), 0 -2px 11px 0 rgba(245,166,35,0.31);
      border-radius: 34px;
      margin: 0 auto;
      .logo {
        width: 72px;
        height: 72px;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 20px 0 40px;
      }
      .right {
        text-align: left;
        flex: 1;
        .top {
          padding: 30px 0 4px;
          font-size: 32px;
          font-weight: 900;
          color: #FF7F18;
          line-height: 45px;
        }
        .wechat {
          padding-bottom: 34px;
          font-size: 26px;
          line-height: 36px;
        }
      }
    }
    .button {
      background: #F5A623;
      border-radius: 30.5px;
      width: 168px;
      height: 60px;
      color: #fff;
      font-size: 32px;
      font-weight: 900;
      line-height: 64px;
      margin: 24px auto 40px;
      position: relative;
    }
    .confirmTip {
      font-size: 28px;
      color: #333333;
      text-align: center;
      line-height: 40px;
      padding-bottom: 30px;
    }
  }
  .close {
    width: 60px;
    height: 60px;
    position: absolute;
    background: url('../../assets/img/task/close.png') no-repeat center;
    background-size: 46px 46px;
    bottom: -100px;
    left: 50%;
    transform: translateX(-50%);
  }
}
</style>



