      </div>
      <div class="task-top">
        <div class="shareIconButton" @click="showAppShare" v-if="$config.source_style >= 6"></div>
        <div class="flex-row header-top jusB">
          <header-loop v-if="headerList.length > 0 && $route.path === '/task'" :list='headerList'></header-loop>
          <div class="flex-row" @click="toPage('/earn_money')">
            <div class="iconSize coin" >
              <img src="../../assets/img/my/coin.png" width="22" alt="">
            </div>
