            {{remainText}}
          </div>
        </div>
        <!--  -->
        <div class="taskItem" :class="{isExpand: !item.isExpand}" v-if="taskList.length > 0" v-for="(item, index) in taskList" :key="index">
          <div class="taskTitle flex-row jusB" @click="setNewTask(index)">
            <div class="titleText">{{item.name}}</div>
            <div class="rightIcon flex-row">
              <span :class="{vcoin: item.reward_type === 2}">+{{item.value}}</span>
              <span class="coinText iconSize" v-if="item.reward_type === 2">&nbsp;</span>
              <div class="coinIcon iconSize" v-else :class="{juejinbao: item.reward_type === 2}">
                <img src="../../assets/img/task/juejinbi.png" alt="">
              </div>
              <div class="arrow">
                <div class="bottomArrow bgIcon"></div>
              </div>
            </div>
          </div>
          <div :ref="'newTask' + index" class="taskContentBox" :style="item.isExpand ? `height: ${item.height}px`: ''" :class="{isExpand: item.isExpand}">
            <div class="taskContent flex-row jusB">
              <div class="description" v-html="item.desc"></div>
              <div class="rightButton" v-if="item.do !== 1" @click="doTask(index, 1)">{{item.tag === 'follow' ? item.do === 2 ? '': '' : item.buttonText}}</div>
              <div class="rightButton gray" v-else></div>
            </div>
          </div>
        </div>
        <div class="rect"></div>
        <!--  -->
        <div class="listTitle flex-row jusB">
          <div class="left flex-row" :class="{active: isActive}">
            <div class="leftline"></div>
            <div class="text"></div>
          </div>
        </div>
        <div class="taskItem" :class="{isExpand: !item.isExpand}" v-for="(item, index) in dailyList" :key="('0' + index)">
          <div class="taskTitle flex-row jusB"  @click="setDayliTask(index)">
            <div class="titleText">{{item.name}}</div>
            <div class="rightIcon flex-row">
              <span :class="{vcoin: item.reward_type === 2}">+{{item.value}}</span>
              <span class="coinText iconSize" v-if="item.reward_type === 2">&nbsp;</span>
              <div class="coinIcon iconSize" v-else :class="{juejinbao: item.reward_type === 2}">
                <img src="../../assets/img/task/juejinbi.png" alt="">
              </div>
              <div class="arrow">
                <div class="bottomArrow bgIcon"></div>
              </div>
            </div>
          </div>
          <div class="taskContentBox" :style="item.isExpand ? `height: ${item.height}px` : ''" :ref="'dailyList' + index" :class="{isExpand: item.isExpand}">
            <div class="taskContent flex-row jusB">
              <div class="description" v-html="item.desc"></div>
              <div class="rightButton" v-if="!item.do" @click="doTask(index, 2)">{{item.buttonText}}</div>
              <div class="rightButton" @click="doTask(index, 2)" v-else-if="item.do === 2"></div>
              <div class="rightButton gray" v-else-if='item.do === 1'>
                <span v-if="item.tag === 'treasure_box'">{{countTime ? countTime : ''}}</span>
                <span v-else>{{item.buttonComplete ? item.buttonComplete : ''}}</span>
              </div>
            </div>
          </div>
        </div>
        <!--  -->
        <div class="linkBox">
          <div class="link flex-row" @click="toPage('/task/quesion')"><div class="arrowEl"></div></div>
        </div>
      </div>
      </mt-loadmore>
    </div>
    <!-- <div class="task" @click="toPage('/saveimage')"></div> -->
    <openBox @openBox='openBox' class="openBox" :class="{isBrowser: $config.source_style < 6}" @getBoxTime='getBoxTime' :isChange='isChange'></openBox>
    <openBoxTip :rewardParams='rewardParams' @closeOpenBox='closeOpenBox' @boxshare='boxshare' v-if="showBoxTip"></openBoxTip>
    <coinReward :RewardInfo='RewardInfo' @click="closeReward" v-if="showReward"></coinReward>
    <pushBox v-if="showPushBox" @closePage='closePage'></pushBox>
    <ShareCommon v-if="showShare" :shareParams='shareParams' @closeShare='closeShare' @showSharePage='showSharePage'></ShareCommon>
    <bindPhone v-if="showBindPhone" :phoneParams='{title: "", confirm: ""}' :errChange='codeUpdate' @closeBindPhone="closeBindPhone"></bindPhone>
    <ConfirmTip v-if="showConfirm" @closeConfirm='closeConfirm'></ConfirmTip>
    <ConnectWechat v-if="showWechat" @closeWechat='closeWechat'></ConnectWechat>
    <confirmBox v-if="showConfirmBox" :config='configText' @closeConfirmBox='closeConfirmBox'></confirmBox>
    <!-- app -->
    <IndexShare @shareSuccess='shareSuccess' @getArc='getArc' v-if="$route.path === '/task'" tag='index' :appShare='appShare'></IndexShare>
    <!--  -->
    <shareBox v-if="treasureBoxShare" :showTreasureBox='showTreasureBox' @shareAction='shareAction'></shareBox>
    <ChooseAccount v-if="showChoose" :config='configText' @closeChoose='closeChoose'></ChooseAccount>
    <download></download>
  </div>
  
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import headerLoop  from '@/components/task/header-loop'
import coinReward from '@/components/common/getCoin'
import coin from '@/components/task/coin'
import pushBox from '@/components/task/push_box'
import openBox from '@/components/task/open_box'
import openBoxTip from '@/components/task/open_box_tip'
import ShareCommon from '@/components/share/shareCommon'
import bindPhone from '@/components/common/bind_phone'
import ConfirmTip from '@/components/task/confirm'
import ConnectWechat from '@/components/task/connect_wechat'
import confirmBox from '@/components/task/confirmBox'
import IndexShare from '@/components/share/IndexShare'
import shareBox from '@/components/common/share_box'
import ChooseAccount from '@/components/task/chooseAccount'
import download from '@/components/common/download'

export default {
  data () {
    return {
      configText: {
        wechat_nickname: '',
        mobile_nickname: '0003',
        mobile_vcoin: 154356,
        wechat_vcoin: 464563
      },
      topStatus: '',
      showChoose: false,        // 
      showWechat: false,        // 
      showConfirm: false,       // 
      showBindPhone: false,     // 
      showConfirmBox: false,    // 
      auths: {},                // 
      showBoxTip: false,         // 
      rewardParams: {},         // 
      showPushBox: false,       // 
      browerType: false,        //  trueapp
      showShare: false,         // 
      shareParams: {},          // 
      days_remaining: 0,        // 
      inviteCoin: 0,            // 
      showTreasureBox: false,   // 
      treasureBoxShare: false,  // app
      signData: {
        list: [18, 38, 58, 78, 188, 288, 388]
      },                        // 
      codeUpdate: 0,
      isChange: 0,              // 
      isActive: true,           // 
      headerList: [],           // 
      showReward: false,        // 
      hasTask: false,           // 
      appShare: {
        count: 0
      },                        // app
      countTime: '',
      remainText: '',
      taskList: [{
        name: '',
        tag: 'wealth_plan',
        buttonText: '',
        buttonComplete: '',
        url: 'MakeMoney'
      }, {
        name: '',
        tag: 'wechat_certification',
        buttonText: '',
        buttonComplete: '',
        url: ''
      }, {
        name: '',
        tag: 'fill_invitation_code',
        buttonText: '',
        buttonComplete: '',
        url: 'setinvitecode'
      }, {
        name: '',
        tag: 'questionnaire_investigation',
        buttonText: '',
        buttonComplete: '',
        url: 'questionnaire'
      }, {
        name: '',
        tag: 'inviting_friend_first',
        buttonText: '',
        buttonComplete: '',
        url: 'invitefriend'
      }, {
        name: '',
        tag: 'phone_bind',
        buttonText: '',
        buttonComplete: '',
        url: ''
      }],
      dailyList: [{
        name: '',
        tag: 'inviting_friend',
        buttonText: '',
        buttonComplete: '',
        url: '/invite_friend'
      }, {
        name: '',
        tag: 'treasure_box_share',
        buttonText: '',
        url: '/openTreaSureBox'
      }, {
        name: '',
        tag: 'treasure_box',
        buttonText: '',
        buttonComplete: '',
        url: '/openTreaSureBox'
      }, {
        name: '',
        tag: 'reading_article',
        buttonText: '',
        buttonComplete: '',
        url: '/'
      }, {
        name: '60',
        tag: 'read_60minute',
        buttonText: '',
        buttonComplete: '',
        url: '/'
      }, {
        name: '',
        tag: 'exposure_to_income',
        buttonText: '',
        buttonComplete: '',
        url: '/my/profit'
      }, {
        name: '',
        tag: 'reading_push_article',
        buttonText: '',
        buttonComplete: '',
        url: ''
      }, {
        name: '',
        tag: 'sharing_article',
        buttonText: '',
        buttonComplete: '',
        url: '/'
      }, {
        name: '',
        tag: 'follow',
        buttonText: '',
        buttonText1: '',
        buttonComplete: '',
        desc: '100',
        value: '100',
        url: ''
      }],
      allTaskList: []
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations([
      'common_login_box',
      'common_set_guild',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_columnid_change',
      'common_set_hasRed'
    ]),
    // 
    translateChange (translate) {
      const translateNum = +translate
      this.translate = translateNum.toFixed(2)
      this.moveTranslate = (1 + translateNum / 140).toFixed(2)
    },
    // 
    handleTopChange (status) {
      this.moveTranslate = 2
      this.topStatus = status
    },
    loadTop () {
      this.getTaskList(res => {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded()
        }, 400)
      })
    },
    // 
    shareAction (actions) {
      if (actions === 'cancel') {
        this.treasureBoxShare = false
      } else {
        let index = 0
        switch (actions) {
          case 'wechat': index = 1
            break
          case 'friend': index = 2
            break
          case 'qq': index = 3
            break
          default: console.log(actions)
        }
        this.currentShare(index)
      }
    },
    // app 
    currentShare (index) {
      if (this.$config.source_style >= 6) {
        // index 1  2  3 qq
        let msg = {
          title: this.shareParams.title,
          content: this.shareParams.desc,
          thumbs: [this.shareParams.img_url],
          href: this.shareParams.shareLink
        }
        let id = 'weixin'
        if (index === 1) {
          id = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneSession'
          }
        } else if (index === 2) {
          id = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneTimeline'
          }
        } else if (index === 3) {
          id = 'qq'
          // msg.type = 'text'
        }
        let w = window.plus.nativeUI.showWaiting('')
        setTimeout(() => {
          w.close()
        }, 5000)
        this.commonState.shares[id].send(msg, res => {
          if (index === 3) {
            w.close()
            this.$toast({
              message: '',
              duration: 1500
            })
          }
          this.treasureBoxShare = false
          this.showTreasureBox = true
          this.shareSuccess()
        }, err => {
          w.close()
          this.treasureBoxShare = false
          if (err.code === -2) {
            this.$toast({
              message: '',
              duration: 1500
            })
          } else {
            this.$toast({
              message: '',
              duration: 1500
            })
          }
        })
      }
    },
    // 
    getBoxTime (e) {
      if (e) {
        this.countTime = e
        if (this.countTime <= 0) {
          this.getTaskList()
        }
      }
    },
    // 
    shareSuccess (e, tag) {
      this.showShare = false
      if (this.showTreasureBox) {
        tag = 'treasure_box'
      } else {
        if (!tag) {
          return
        }
      }
      this.$api.get('CALLBACK_REWARD', {tag}, res => {
        this.showShare = false
        this.showTreasureBox = false
        console.log(this.showShare, this.showTreasureBox)
        if (res.data.coin > 0) {
          let data = res.data
          data.type = this.commonState.shareArc['treasure_box']
          this.set_shareType(7)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj: data
          })
          this.setShowShare(true)
        }
      })
    },
    // app
    showAppShare () {
    /*
     * rsCode:1
     * token:token
     * msg:message
     */  
     var params = {'way':'dingding','title':'','conent':''}
      plus.myplugin.shareTo('dingding',params, function (jsonObj){
        alert(jsonObj.rsCode + '=' + jsonObj.token + 'msg='+jsonObj.msg)
      }, null)
      // if (!this.commonState.login_bol) {
      //   return this.common_login_box(true)
      // }
      // this.appShare.count++
    },
    // 
    closeConfirmBox ({action}) {
      if (action === 'confirm') {
        this.showConfirmBox = false
        this.showChoose = true
      } else if (action === 'cancel') {
        this.showConfirmBox = false
      } else {
        this.showConfirmBox = false
      }
    },
    // 
    closeChoose ({action, type}) {
      if (action === 'confirm') {
        if (this.$config.source_style >= 6) {
          // app
          let w = window.plus.nativeUI.showWaiting('')
          this.auths['weixin'].authorize(res => {
            let data = {
              code: res.code,
              confirm: 1,
              retain_wechat: type
            }
            this.$api.post('APP_BIND', data, res => {
              this.bindSuccess(res)
            }, res => {
              if (res.code === 801) {
                this.configText = res.data
                this.showConfirmBox = true
                this.showWechat = false
              }
            })
            w.close()
          }, (res) => {
            w.close()
            this.$toast({
              message: '',
              duration: 1500
            })
          })
        } else {
          // 
          let data = {
            unionid: this.$utils.cache('unionid'),
            confirm: 1,
            retain_wechat: type
          }
          this.$api.post('WECHAT_BIND', data, res => {
            this.bindSuccess(res)
          }, res => {
            if (res.code === 801) {
              this.configText = res.data
              this.showWechat = false
              this.showConfirmBox = true
            }
          })
        }
      } else {
        this.showChoose = false
      }
    },
    // 
    bindSuccess (res) {
      this.showChoose = false
      if (res.data.coin > 0) {
        let shareSuccessObj = Object.assign({type: 3, tipText: `${this.$utils.splitStr(res.data.wechat_nickname)}`}, res.data)
        this.set_shareSuccessObj({
          type: 16,
          shareSuccessObj
        })
        this.set_shareType(16)
        this.setShowShare(true)
      } else {
        this.$toast({
          message: `${this.$utils.splitStr(res.data.wechat_nickname)}`,
          duration: 1500
        })
      }
      this.showWechat = false
      this.showConfirmBox = false
      // 
      this.getUserInfo()
      this.getTaskList()
    },
    // 
    getUserInfo () {
      this.$api.get('GET_USERINFO', {}, res => {
        this.common_set_userInfo(Object.assign(this.commonState.userInfo, res.data))
      })
    },
    // e
    closeWechat (e) {
      if (e) {
        if (this.$config.source_style >= 6) {
          // app
          let w = window.plus.nativeUI.showWaiting('')
          this.auths['weixin'].authorize(res => {
            let data = {
              code: res.code
            }
            this.$api.post('APP_BIND', data, res => {
              this.bindSuccess(res)
            }, res => {
              if (res.code === 801) {
                this.configText = res.data
                this.showConfirmBox = true
                this.showWechat = false
              }
            })
            w.close()
          }, () => {
            w.close()
            this.$toast({
              message: '',
              duration: 1500
            })
          })
          // 
        } else {
          let data = {
            unionid: this.$utils.cache('unionid')
          }
          this.$api.post('WECHAT_BIND', data, res => {
            this.bindSuccess(res)
          }, res => {
            if (res.code === 801) {
              this.configText = res.data
              this.showWechat = false
              this.showConfirmBox = true
            }
          })
        }
      } else {
        this.showWechat = false
      }
    },
    // 
    closeConfirm () {
      this.showConfirm = false
    },
    // 
    closeBindPhone (e) {
      if (e) {
        let data = {
          account: e.mobile,
          code: e.code
        }
        if (e.captcha_code) {
          data.captcha_code = e.captcha_code
        }
        this.$api.post('BIND_PHONE', data, res => {
          if (res.data.coin > 0) {
            let shareSuccessObj = Object.assign({type: 5}, res.data)
            this.set_shareSuccessObj({
              type: 7,
              shareSuccessObj
            })
            this.set_shareType(7)
            this.setShowShare(true)
            this.getTaskList()
            this.showBindPhone = false
          } else {
            this.$toast({
              message: '',
              duration: 1500
            })
          }
        }, res => {
          if (res.code === 10) {
            this.codeUpdate++
          }
        })
      } else {
        this.showBindPhone = false
      }
    },
    // 
    getArc (e) {
      if (e) {
        this.shareParams = e
      }
    },
    // 
    getArcRandom (tag = 'treasure_box') {
      let data = {tag, url: location.href}
      this.$api.get('RAMDOM_ARC', data, res => {
        this.shareParams = res.data
        if (localStorage.getItem('domain')) {
          this.$config.currentWebUrl = localStorage.getItem('domain')
        }
        if (this.$config.source_style < 6) {
          this.shareParams.link = `${location.origin}?path=/share_page/${Math.ceil(Math.random() * 3)}&inviteCode=${this.commonState.userInfo.invitation}&tag=${tag}`
        } else {
          this.shareParams.link = `${this.$config.currentWebUrl}?path=/share_page/${Math.ceil(Math.random() * 3)}&inviteCode=${this.commonState.userInfo.invitation}&tag=${tag}`
        }
        // 
        if (this.$utils.isWx()) {
          this.configShare()
        }
      })
    },
    // 
    closeShare () {
      this.showShare = false
    },
    //  ()
    showSharePage () {
      // this.showShare = true
      this.$router.push('/short_link?tag=treasure_box')
    },
    // 
    closeOpenBox () {
      this.showBoxTip = false
    },
    // app
    boxshare () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (this.$config.source_style >= 6) {
        // 
        this.showTreasureBox = true
        // 
        this.treasureBoxShare = true
        // 
        this.showBoxTip = false
      } else if (this.$utils.isWx()) {
        this.showTreasureBox = true
        this.showShare = true
        this.closeOpenBox()
      }
    },
    // 
    openBox () {
      this.$api.get('OPEN_BOX_REWARD', {}, res => {
        if (!res.data.err_code) {
          this.rewardParams = res.data
          this.showBoxTip = true
          this.isChange++
          this.getTaskList()
        }
      })
    },
    // 
    closeReward () {
      this.showReward = false
    },
    closePage () {
      this.showPushBox = false
    },
    // 
    setDayliTask (index) {
      let c
      if (this.dailyList[index].isExpand) {
        c = false
      } else {
        c = true
      }
      this.$set(this.dailyList[index], 'isExpand', c)
    },
    toUrl (name) {
      this.$router.push({name})
    },
    // 
    setNewTask (index) {
      let c
      if (this.taskList[index].isExpand) {
        c = false
      } else {
        c = true
      }
      this.$set(this.taskList[index], 'isExpand', c)
    },
    // 
    getFirstFriend () {
      let a = false
      if (a) {
      } else {
        this.$router.push('/invite_friend')
      }
    },
    // 
    wechat_certification (str, index) {
      if (this.$config.source_style >= 6) {
        this.showWechat = true
      } else {
        if (this.$utils.isWx()) {
          this.showWechat = true
        } else {
          this.$toast({
            message: 'app'
          })
        }
      }
    },
    // 
    setConfirm (str, index) {
      let temp = this[str][index]
      if (!temp.do) {
        this.showConfirm = true
      } else if (temp.do === 2) {
        this.$api.get('GET_TASKFOLLOW', {}, res => {
          this.getTaskList()
          if (res.data.coin > 0) {
            let shareSuccessObj = Object.assign({type: 7}, res.data)
            this.set_shareSuccessObj({
              type: 7,
              shareSuccessObj
            })
            this.set_shareType(7)
            this.setShowShare(true)
            this.getTaskList()
            // 3
          }
        }, res => {
          if (res.code === 2) {
            this.$toast({
              message: res.msg
            })
          }
        })
      } else {
        
      }
    },
    //   0  type(treasure_box, treasure_box_share)
    openTreasureBox (type) {
      if (type === 'treasure_box') {
        this.$api.get('OPEN_BOX_TIME', {}, res => {
          if (res.data <= 0) {
            this.openBox()
          } else if (this.$utils.getTimeStamp() >= res.data) {
            this.openBox()
          } else {
            this.$toast({
              message: '',
              duration: 1500
            })
          }
        })
      } else if (type === 'treasure_box_share') {
        this.toPage('/short_link?tag=treasure_box')
      }
    },
    /** 
     *@param {Number} index 
     *@param {Number} type  1 2
     */
    doTask (index, type) {
      let item = {}
      let str = ''
      if (type === 1) {
        item = this.taskList[index]
        str = 'taskList'
      } else if (type === 2) {
        item = this.dailyList[index]
        str = 'dailyList'
      }
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      switch (item.tag) {
        case 'follow':
          this.setConfirm(str, index)
          break
        // 
        case 'fill_invitation_code':
          this.$router.push(item.url)
          break
        // 
        case 'inviting_friend':
          this.$router.push(item.url)
          break
        // 
        case 'wealth_plan':
          setTimeout(() => {
            this.$toast({
              message: '',
              duration: 3000
            })
          }, 400)
          this.$router.push({
            name: item.url,
            query: {
              task: true
            }
          })
          break
        //  
        case 'wechat_certification' :
          this.wechat_certification(str, index)
          break
        // 
        case 'inviting_friend_first' :
          this.getFirstFriend()
          break
        // 
        case 'questionnaire_investigation' :
          this.$router.push(item.url)
          break
        // 
        case 'phone_bind' :
          this.showBindPhone = true
          break
        // 60
        case 'read_60minute' :
          this.$router.push(item.url)
          break
        // 
        case 'reading_article' :
          this.$toast({
            message: '',
            duration: 1500
          })
          this.$router.push(item.url)
          break
        // 
        case 'exposure_to_income' :
          this.$router.push(item.url)
          break
        // 
        case 'treasure_box' :
          this.openTreasureBox('treasure_box')
          break
        // 
        case 'treasure_box_share' :
          this.openTreasureBox('treasure_box_share')
          break
        // 
        case 'reading_push_article' :
          this.showPushBox = true
          break
        // 
        case 'sharing_article' :
          this.$router.push(item.url)
          this.common_set_columnid_change()
          this.$toast({
            message: '',
            duration: 1500
          })
          break
        default : console.log(item.name)
      }
    },
    // 
    toPage (url, needLogin) {
      if (needLogin && !this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.$router.push(url)
    },
    // 
    login () {
      this.common_login_box(true)
    },
    // 
    getHeaderList () {
      this.$api.get('REWARD_LIST', {}, res => {
        this.headerList = res.data
      })
    },
    // 
    initTimeRemain () {
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
        return
      }
      let that = this
      countText()
      this.timer = setInterval(() => {
        this.days_remaining -= 30
        countText()
        if (this.days_remaining <= 0 && this.taskList.length > 0) {
          clearInterval(this.timer)
          return this.getTaskList()
        }
      }, 30000)
      function countText () {
        if (that.commonState.login_bol) {
          if (that.days_remaining >= 0) {
            let days = parseInt(that.days_remaining / 86400 * 30)
            let hours = parseInt((that.days_remaining - 86400 * days) / 3600)
            let minutes = parseInt((that.days_remaining - 86400 * days - hours * 3600) / 60)
            hours = hours >= 10 ? hours : '0' + hours
            minutes = minutes >= 10 ? minutes : '0' + minutes
            that.remainText = `${days}${hours}${minutes}`
          } else {
            that.remainText = '30'
          }
        } else {
          that.remainText = '30'
        }
      }
    },
    // 
    getTaskList (cb) {
      this.$api.get('TASK_LIST', {}, res => {
        this.hasTask = true
        typeof cb === 'function' && cb(res)
        this.days_remaining = res.data.datetime_remaining
        // this.days_remaining = 86210
        this.inviteCoin = res.data.inviting_friend_first
        this.initTimeRemain()
        if (res.data.day_list) {
          let arr = []
          for (let v of res.data.day_list) {
            for (let v1 of this.allTaskList) {
              if (v.tag === v1.tag) {
                if (v.tag === 'sharing_article') {
                  // 
                  if (this.$utils.isWx() || this.$config.source_style >= 6) {
                    v.desc = '<div class="shareButton"></div>' + v.desc
                  } else {
                    v.desc = '' + v.desc
                  }
                  arr.push(Object.assign(v1, v, {do: v.do}))
                } else if (this.$utils.isWx() || this.$config.source_style >= 6) {
                  arr.push(Object.assign(v1, v, {do: v.do}))
                } else {
                  if (v.tag !== 'wechat_certification') {
                    arr.push(Object.assign(v1, v, {do: v.do}))
                  }
                }
              }
              continue
            }
          }
          this.dailyList = arr
        } else {
          this.dailyList = []
        }
        if (res.data.first) {
          let arr = []
          for (let v of res.data.first) {
            for (let v2 of this.allTaskList) {
              if (v.tag === v2.tag) {
                if (this.$config.source_style >= 6 || this.$utils.isWx()) {
                  arr.push(Object.assign(v2, v))
                } else {
                  if (v.tag !== 'wechat_certification') {
                    arr.push(Object.assign(v2, v))
                  }
                }
              }
            }
          }
          this.taskList = arr
        } else {
          this.taskList = []
        }
        this.$nextTick(() => {
          this.getListHeight()
        })
      })
    },
    // 
    getListHeight () {
      if (this.dailyList && this.dailyList.length > 0) {
        for (let i of this.dailyList.keys()) {
          let tempEl = this.$refs['dailyList' + i][0].querySelector('.taskContent')
          let height = tempEl.clientHeight
          this.$set(this.dailyList[i], 'height', height)
        }
      }
      if (this.taskList && this.taskList.length > 0) {
        for (let j of this.taskList.keys()) {
          let tempEl = this.$refs['newTask' + j][0].querySelector('.taskContent')
          let height = tempEl.clientHeight
          this.$set(this.taskList[j], 'height', height)
        }
      }
    },
    // 
    signFn () {
      if (this.$route.path !== '/task') {
        return
      }
      this.$api.get('SIGNIN', {}, res => {
        this.signData = res.data
        if (this.commonState.login_bol) {
          let showGuild = this.$utils.cache('showGuild') ? JSON.parse(this.$utils.cache('showGuild')) : null
          if (res.data.prompt_reward * 1 === 1) {
            let shareSuccessObj = Object.assign({type: 8}, res.data)
            this.set_shareSuccessObj({
              type: 7,
              shareSuccessObj
            })
            // 
            let hasRed = Object.assign(this.commonState.hasRed)
            hasRed['/task'] = false
            this.common_set_hasRed(hasRed)
            this.set_shareType(7)
            this.setShowShare(true)
          }
          if ((!showGuild) || showGuild.isShow !== 1) {
            let timeStamp = this.$utils.timestampToStr(new Date().getTime(), 'Y/M/D')
            timeStamp = parseInt(new Date(timeStamp + ' 23:59:59').getTime() / 1000)
            let obj = {
              isShow: 1
            }
            obj.time = timeStamp
            if (!showGuild) {
              obj.count = 1
            } else {
              obj.count = showGuild.count * 1 + 1
            }
            this.$utils.cache('showGuild', JSON.stringify(obj))
            this.common_set_guild(false)
          }
        }
      })
    },
    // 
    getWXLogin () {
      this.auths = {}
      this.$utils.plusReady((plus) => {
        plus.oauth.getServices(services => {
          for (let i in services) {
            let tempService = services[i]
            this.auths[tempService.id] = tempService
          }
        }, services => {
          this.$toast({
            message: '',
            duration: 1500
          })
        })
      })
    },
    initData () {
      this.getHeaderList()
      this.signFn()
      this.getTaskList()
      this.getWXLogin()
    }
  },
  watch: {
    'commonState.login_box': function (newValue) {
      if (!newValue && this.commonState.login_bol && this.$route.path === '/task') {
        this.initData()
      }
    },
    $route () {
      this.showBoxTip = false
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      // 
      this.getTaskList()
      this.signFn()
    }
  },
  created () {
    this.allTaskList = this.taskList.concat(this.dailyList)
    if (!this.$utils.isWx()) {
      this.browerType = true
    }
    this.initData()
  },
  activated () {
    this.$store.commit('common_set_imgIndex', 2)
  },
  // 
  deactivated () {
    this.$utils.cache('taskScroll', this.$utils.scrollHeight())
  },
  components: {
    headerLoop,       // 
    coin,             // 
    coinReward,       // 
    pushBox,          // 
    openBox,          // 
    openBoxTip,       // 
    ShareCommon,      // 
    bindPhone,        // 
    ConfirmTip,       // 
    ConnectWechat,    // 
    confirmBox,       // 
    IndexShare,       // 
    shareBox,         // 
    ChooseAccount,
    download
  }
}

</script>
<style lang="scss" scoped>
