  &.isBrowser {
    bottom: 280px!important;
  }
}
.mint-loadmore-top {
    margin-top: -142px;
    height: 140px;
    padding-top: 30px;
    box-sizing: border-box;
    transition: all margin .4s;
    &.loading {
      margin-top: -100px;
    }
    .text{
      white-space: nowrap;
      text-align: center;
      font-size: 22px;
      color: #999;
    }
  }
  .loading-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666;
    font-size: 28px;
    margin: 0 auto 20px;
    color: #ffcc00;
    .logo {
      width: 40px;
      height: 40px;
      margin-right: 10px;
      animation: rotateFn 600ms linear infinite;
    }
    @keyframes rotateFn {
      form {
        transform: rotateZ(0deg);
      }
      to {
        transform: rotateZ(360deg);
      }
    }
  }
  .rotateLoading {
    height: 120px;
    .download-icon{
      width: 60px;
      height: 60px;
      overflow: hidden;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    background-size: 100%;
    margin: 0 auto;
  }
  .taskBox {
    overflow: hidden;
    height: 100vh;
    &.Router {
      transition: all 0s ease!important;
    }
  }
  #task{
    background: #f2f2f2;
    height: 100vh;
    transition: all 0ms;
    color: #232323;
    font-size: 24px;
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 100px;
    overflow: auto;
    &.isBrowser {
      padding-bottom: 230px;
    }
    
    .shareIconButton{
      background-position: 0 25px;
      width: 40px;
      padding-right: 30px;
      right: 0;
    }
    .rect {
      background: #f5f5f5;
      height: 20px;
    }
    .task-top{
      color: #fff;
      position: relative;
      height: 350px;
      background-image: linear-gradient(-180deg, #FF9B00 3%, #F7C611 96%);
      .header-top{
        justify-content: space-between;
        white-space: nowrap;
        padding: 82px 20px 45px;
        .coin{
          width: 48px;
          height: 48px;
          margin-right: 10px;
        }
        .make-money{
          font-size: 28px;
          color: #f00;
          border: 1px solid #f00;
          border-width: 1px;
          height: 42px;
          display: flex;
          align-items: center;
          width: 136px;
          align-content: center;
          padding-top: 6px;
          justify-content: center;
          line-height: 0;
          border-radius: 42px;
        }
      }
      
    }
    // 
    .coinBox {
      width: 710px;
      margin: 0 auto;
      height: 330px;
      background: #fff;
      margin-top: -140px;
      position: relative;
      border-radius: 15px;
      box-shadow: 0 2px 15px rgba($color: #999, $alpha: .15);
      .singTip{
        text-align: left;
        padding: 20px 10px;
        margin-top: -10px;
        min-width: 500px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: 28px;
        .flex-row {
          min-width: 280px;
        }
        .rightContent{
          height: 50px;
          align-items: center;
          .flex-row{
            height: 100%;
            align-items: center;
          }
        }
        .ask{
          width: 42px;
          height: 42px;
          min-width: 42px;
          display: flex;
          align-items: center;
          line-height: 42px;
          font-size: 30px;
          justify-content: center;
          border-radius: 50%;
          background: #FE9A00;
          color: #fff;
          margin-left: 10px;
        }
      }
      .centerBox{
        .topLogin {
          display: flex;
          margin: 0 auto;
          text-align: center;
          justify-content: center;
          padding-bottom: 22px;
        }
        .login{
          font-size: 36px;
          color: #FF3500;
        }
      }
      .logout{
        .newButton{
          font-size: 36px;
          color: #FF3500;
          background: #fff;
          padding-top: 8px;
          border-radius: 50px;
          width: 236px;
          height: 50px;
          border: 1px solid #ff3500;
        }
        .signButton{
          // margin-top: 20px;
          height: 64px;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 64px;
          padding: 0 32px;
          background: url('../../assets/img/task/login_button.png') no-repeat center;
          background-size: 100%;
          color: #FF6400;
          overflow: hidden;
        }
        .autoSign {
          margin-top: 10px;
          line-height: 34px;
          font-size: 24px;
          font-weight: 900;
          color: #BA6907;
        }
      }
      // font-size: 24px;
      // color: #BA6907;
    }
    .coinBox.loginOut{
      height: 358px;
    }
    // banner
    .task_banner {
      width: 710px;
      height: 180px;
      background-image: url('../../assets/img/task/task_banner.png');
      margin: 20px;
      box-shadow: 2px 2px 6px 0 #E0E0E0, -2px -2px 6px 0 #E0E0E0;
      border-radius: 20px;
      overflow: hidden;
      position: relative;
      .vcoin {
        position: absolute;
        left: 250px;
        bottom: 44px;
        font-size: 50px;
        color: #f2cc05;
        width: 90px;
        text-align: center;
      }
    }
    // 
    .taskList{
      padding-bottom: 200px;
      background: #fff;
      position: relative;
      .listTitle{
        height: 90px;
        font-size: 30px;
        background: #fff;
        white-space: nowrap;
        padding: 0 20px;
        .left{
          .leftline {
            width: 4px;
            height: 30px;
            margin-right: 10px;
            background: #232323
          }

        }
        .left.active{
          .leftline {
            background: #FF7F18;
          }
          .text{
            color: #FF7F18;
            font-size: 32px;
          }
        }
        .right{
          color:#B4B4B4;
          font-size: 24px;
          width: 300px;
          justify-content: flex-end;
          .timeicon{
            width: 30px;
            height: 30px;
            background: url('../../assets/img/task/time_icon.png') no-repeat center;
            background-size: 100% 100%;
            margin-right: 10px;
          }
        }
      }
      .taskItem {
        .taskTitle {
          height: 90px;
          background: #fff;
          font-size: 30px;
          border-top: 1px solid #f2f2f2;
          border-bottom: 1px solid #f2f2f2;
          margin-bottom: -1px;
          padding: 0 30px;
