      }
      .linkBox {
        position: absolute;
        bottom: 0;
        height: 200px;
        border-top: 1px solid #f2f2f2;
        left: 0;
        right: 0;
        box-sizing: border-box;
        padding-top: 50px;
        background: #fff;
        .link {
          line-height: 2;
        }
        .arrowEl {
          border-color:#3CA5E9;
          width: 12px;
          height: 12px;
        }
      }
    }
  }
</style>



