<template>
  <swiper id="headerLoop"  :options="swiperOption" ref="swiperContainer">
    <!-- slides -->
    <swiper-slide class="swiper-slide" v-for="(item, index) in list" v-if="item.nickname" :key="index">
      <div class="left-content">
        <div class="avatar iconSize">
          <img :src="item.avatar" onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';" alt="">
        </div>
      </div>
      <div class="right-content flex-column">
        <div class="nickname">{{item.nickname}}</div>
        <div class="reward"><span class="mainColor">{{item.minutes_text}}</span>&nbsp;{{item.tag_text}}&nbsp; <span v-html="item.remark"></span> </div>
      </div>
    </swiper-slide>
  </swiper>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    let that = this
    return {
      swiperOption: {
        el: that.$refs.swiperContainer,
        direction: 'vertical',
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        loop: true
      }
    }
  },
  props: {
    list: {
      type: Array // 
    }
  }
}
</script>
<style lang="scss">
  #headerLoop {
    max-width: 462px;
    height: 90px;
    border-radius: 45px;
    margin: 0;
    color: #fff;
    font-size: 24px;
    background: rgba($color: #000000, $alpha: .5);
    overflow: hidden;
    .swiper-wrapper{
      overflow-x:hidden;
      width: 100%;
      height: auto!important;
    }
    .swiper-slide{
      display: flex;
      padding: 0 20px;
      box-sizing: border-box;
      align-items: center;
      height: 90px;
      .left-content{
        .avatar {
          width: 72px;
          height: 72px;
          margin-right: 16px;
          overflow: hidden;
          border-radius: 50%;
        }
      }
      .right-content{
        flex: 1;
        min-width: 0;
        >div{
          width: 100%;
          text-align: left;
          line-height: 1.3;
        }
        .reward{
          min-width: 0;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
      }
    }
    
  }
</style>



